  .header {
    padding-top: 32px;
  }
}
.header__container {
  display: flex;
  align-items: center;
  gap: 40px;
}
.header__nav {
  display: flex;
  align-items: center;
  flex-grow: 1;
  gap: 20px;
}
.header__menu {
  display: none;
  text-wrap: nowrap;
  font-weight: 500;
  font-size: 16px;
  line-height: 160%;
}
@media screen and (min-width: 992px) {
  .header__menu {
    display: flex;
    gap: 16px;
    flex-grow: 1;
    justify-content: flex-end;
  }
}
@media screen and (min-width: 1280px) {
  .header__menu {
    gap: 24px;
  }
}
.header__button {
  display: none;
}
@media screen and (min-width: 992px) {
  .header__button {
    display: block;
  }
}
.header__burger {
  background-color: transparent;
  border: none;
  height: 24px;
  width: 24px;
  padding: 0;
  fill: var(--color-primary);
}
@media screen and (min-width: 992px) {
  .header__burger {
    display: none;
  }
}

.hero-section {
  padding-top: 124px;
  position: relative;
  background-color: #fff5ec;
  background-color: var(--primary);
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .hero-section {
    padding-top: 150px;
  }
}
@media screen and (min-width: 1280px) {
  .hero-section {
    padding-top: 165px;
  }
}
.hero-section__container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .hero-section__container {
    flex-direction: row;
    gap: 60px;
  }
}
.hero-section__content {
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.hero-section__title {
  position: relative;
  font-family: "Poppins", sans-serif;
  font-family: var(--second-family);
  color: #0bd0d0;
  color: var(--color-accent);
  font-weight: 700;
  font-size: 60px;
  line-height: 96%;
  letter-spacing: -0.03em;
  margin: 0;
}
.hero-section__text {
  font-weight: 400;
  font-size: 20px;
  line-height: 160%;
}
.hero-section__bottom::before {
  content: "";
  border-bottom: 1px solid rgba(0, 0, 0, 0.09);
  display: block;
  height: 1px;
  width: 100%;
  margin-bottom: 14px;
}
.hero-section__bottom-text {
  font-family: "Poppins", sans-serif;
  font-family: var(--second-family);
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  color: #0393e5;
  color: var(--secondary);
}
.hero-section__button {
  margin-left: 0;
  width: -moz-fit-content;
  width: fit-content;
  text-wrap: nowrap;
}
.hero-section__img {
  width: 70%;
  -o-object-fit: contain;
     object-fit: contain;
}
@media screen and (min-width: 768px) {
  .hero-section__img {
    width: 50%;
  }
}

.life-section__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}
.life-section__item:nth-child(odd) {
  background-color: #f5f9fe;
  background-color: var(--secondary-light);
}

.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  width: 100%;
  height: 100%;
  padding: 16px 24px;
  background-color: #fff5ec;
  background-color: var(--primary);
  transform: translateX(100%);
  transition: transform 0.3s;
}
.mobile-menu.is-open {
  transform: translateX(0);
}
.mobile-menu__btn-close {
  position: absolute;
  top: 16px;
  right: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  fill: transparent;
  stroke: #0393e5;
  stroke: var(--secondary);
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.mobile-menu__list {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding-top: 40px;
}
.mobile-menu__link {
  color: var(--color-primary);
  font-size: 16px;
  line-height: 1.5;
  text-decoration: none;
  font-weight: 600;
}
.mobile-menu__link:hover {
  color: #0393e5;
  color: var(--secondary);
}

.section {
  padding: 59px 0;
}
@media screen and (min-width: 768px) {
  .section {
    padding: 88px 0;
  }
}
.section__title {
  color: #0f2f64;
  color: var(--color-text-primary);
  font-weight: 600;
  font-size: 50px;
  line-height: 120%;
}
@media screen and (min-width: 768px) {
  .section__title {
    font-size: 48px;
  }
}
.section__title--centered {
  text-align: center;
}
.section__subtitle {
  font-weight: 400;
  font-size: 18px;
  line-height: 163%;
  text-align: center;
  opacity: 0.8;
}

.thank-you {
  text-align: center;
  padding-top: 124px;
}
.thank-you__button {
  width: 20%;
  margin: 0px auto 40px;
